<template>
  <el-row :gutter="40" class="panel-group">
    <el-col :xs="12" :sm="12" :lg="6" class="card-panel-col">
      <div class="card-panel">
        <div class="card-panel-icon-wrapper icon-people">
          <i class="fa fa-microchip"></i>
          <!--<svg-icon icon-class="peoples" class-name="card-panel-icon" />-->
        </div>
        <div class="card-panel-description">
          <el-progress type="circle" :color="colors" :width="80" :percentage="cpu"></el-progress>
        </div>
      </div>
    </el-col>
    <el-col :xs="12" :sm="12" :lg="6" class="card-panel-col">
      <div class="card-panel">
        <div class="card-panel-icon-wrapper icon-message">
          <!--<svg-icon icon-class="message" class-name="card-panel-icon" />-->
        </div>
        <div class="card-panel-description">
          <el-progress type="circle" :width="80" :percentage="25"></el-progress>
          <el-button @click="cpu+=10"></el-button>
        </div>
      </div>
    </el-col>
    <el-col :xs="12" :sm="12" :lg="6" class="card-panel-col">
      <div class="card-panel">
        <div class="card-panel-icon-wrapper icon-money">
          <!--<svg-icon icon-class="money" class-name="card-panel-icon" />-->
        </div>
        <div class="card-panel-description">
          <el-progress type="circle" :width="80" :percentage="25"></el-progress>
        </div>
      </div>
    </el-col>
    <el-col :xs="12" :sm="12" :lg="6" class="card-panel-col">
      <div class="card-panel">
        <div class="card-panel-icon-wrapper icon-shopping">
          <!--<svg-icon icon-class="shopping" class-name="card-panel-icon" />-->
        </div>
        <div class="card-panel-description">
          <el-progress type="circle" :width="80" :percentage="25" :color="colors"></el-progress>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: "System",
  data() {
    return {
      cpu:0,
      colors: [
        {color: '#00ff4f', percentage: 40},
        {color: '#5cb87a', percentage: 40},
        {color: '#f8d166', percentage: 60},
        {color: '#ff9d00', percentage: 80},
        {color: '#ff0000', percentage: 100}
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.panel-group {
  margin-top: 18px;

  .card-panel-col {
    margin-bottom: 32px;
  }

  .card-panel {
    height: 108px;
    cursor: pointer;
    font-size: 12px;
    position: relative;
    overflow: hidden;
    color: #666;
    background: #fff;
    box-shadow: 4px 4px 40px rgba(0, 0, 0, .05);
    border-color: rgba(0, 0, 0, .05);

    &:hover {
      .card-panel-icon-wrapper {
        color: #fff;
      }

      .icon-people {
        background: #40c9c6;
      }

      .icon-message {
        background: #36a3f7;
      }

      .icon-money {
        background: #f4516c;
      }

      .icon-shopping {
        background: #34bfa3
      }
    }

    .icon-people {
      color: #40c9c6;
    }

    .icon-message {
      color: #36a3f7;
    }

    .icon-money {
      color: #f4516c;
    }

    .icon-shopping {
      color: #34bfa3
    }

    .card-panel-icon-wrapper {
      float: left;
      margin: 7px;
      padding: 16px;
      transition: all 0.38s ease-out;
      border-radius: 6px;
      i{
        font-size: 60px;
      }
    }


    .card-panel-icon {
